export default {
	data() {
		return {
			num: 0,
			popups: false
		}
	},
	methods: {
		reduce() {
			var _this = this;
			_this.num--
		},
		add() {
			var _this = this;
			_this.num++
		},
		popupIndex() {
			var _this = this;
			if (_this.popups) {
				_this.popups = false
			} else{
				_this.popups = true
			}
		},
		popupYes() {
			var _this = this;
			_this.popups = false
			uni.navigateTo({
				url:"integral_handle",
			})
		}
	}
}
